import axios from "axios";
import React from "react";
import ReactDOM from "react-dom/client";

import App from "./App";

import "./style.css";

axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
    <div className="body">
      <App />
    </div>
);

